import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import 'vant/lib/index.css';
import {
  Swipe, SwipeItem,
  Lazyload, Badge, Sidebar,
  SidebarItem, Collapse,
  CollapseItem, Tab, Tabs,
  Card, Image as VanImage,
  Tag, Button, Form,
  Field, Checkbox,
  CheckboxGroup, SwipeCell,
  SubmitBar, Empty, Stepper, ActionSheet,
  Cell, CellGroup, AddressList, AddressEdit,
  Area, ContactCard, Popup,
  Grid, GridItem, PullRefresh, List 
} from 'vant';

createApp(App)
  .use(store)
  .use(router)
  .use(Swipe)
  .use(SwipeItem)
  .use(Badge)
  .use(Sidebar)
  .use(SidebarItem)
  .use(Collapse)
  .use(CollapseItem)
  .use(Tab)
  .use(Tabs)
  .use(Card)
  .use(VanImage)
  .use(Tag)
  .use(Button)
  .use(Form)
  .use(Field)
  .use(Checkbox)
  .use(CheckboxGroup)
  .use(SwipeCell)
  .use(SubmitBar)
  .use(Empty)
  .use(Stepper)
  .use(ActionSheet)
  .use(Cell)
  .use(CellGroup)
  .use(AddressList)
  .use(AddressEdit)
  .use(Area)
  .use(ContactCard)
  .use(Popup)
  .use(Grid)
  .use(GridItem)
  .use(PullRefresh)
  .use(List )
  .use(Lazyload, {
    // lazyComponent: true,
    loading: require('./assets/up.png'),
  })
  .mount('#app')
