import { ref } from '@vue/reactivity'
import { createRouter, createWebHistory } from 'vue-router'
import store from '../store'
import { Toast } from "vant";
// import Home from '../views/Home.vue'

const routes = [
  {
    path: '/',
    name: 'DefaultHome',
    component: () => import('views/home/Home.vue'),
    meta: {
      title: '书域'
    }
  },
  {
    path: '/home',
    name: 'Home',
    component: () => import('views/home/Home.vue'),
    meta: {
      title: '书域'
    }
  },
  {
    path: '/category',
    name: 'Category',
    component: () => import('views/category/Category.vue'),
    meta: {
      title: '书域-商品分类'
    }
  },
  {
    path: '/detail',
    name: 'Detail',
    component: () => import('views/detail/Detail.vue'),
    meta: {
      title: '书域-书籍详情'
    }
  },
  {
    path: '/cart',
    name: 'Cart',
    component: () => import('views/cart/Cart.vue'),
    meta: {
      title: '书域-购物车',
      isAuthorization:true
    }
  },
  {
    path: '/proflie',
    name: 'Proflie',
    component: () => import('views/proflie/Proflie.vue'),
    meta: {
      title: '书域-个人中心',
      isAuthorization:true
    }
  },
  {
    path: '/register',
    name: 'register',
    component: () => import('views/proflie/Register.vue'),
    meta: {
      title: '书域-用户注册'
    }
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('views/proflie/Login.vue'),
    meta: {
      title: '书域-用户登录'
    }
  },
  {
    path: '/address',
    name: 'Address',
    component: () => import('views/proflie/Address.vue'),
    meta: {
      title: '书域-地址管理'
    }
  },
  {
    path: '/addressedit',
    name: 'Addressedit',
    component: () => import('views/proflie/Addressedit.vue'),
    meta: {
      title: '书域-地址编辑'
    }
  },
  {
    path: '/orders',
    name: 'Orders',
    component: () => import('views/orders/Orders.vue'),
    meta: {
      title: '书域-提交订单'
    }
  },
  {
    path: '/orderdetail',
    name: 'Orderdetail',
    component: () => import('views/orders/Orderdetail.vue'),
    meta: {
      title: '书域-订单详情'
    }
  },
  {
    path: '/order',
    name: 'Order',
    component: () => import('views/orders/Order.vue'),
    meta: {
      title: '书域-订单列表'
    }
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.beforeEach((to,from,next) => {
  // 如果没有登录，在这里跳到Login
  if(to.meta.isAuthorization && store.state.user.isLogin == false){
    Toast.success("请登录");
    return next('/login');
  }else{
    next();
  }
  
  document.title = to.meta.title;
})

export default router
